/* Settings Component - Touch Optimized */
.settings {
  width: 100%;
  height: 100vh;
  background-color: #2a2d32;
  color: white;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.settings-header {
  display: flex;
  align-items: center;
  padding: 15px 20px;
  background-color: #22252a;
  border-bottom: 2px solid #3c4147;
  min-height: 70px;
  flex-shrink: 0;
}

.back-button {
  background-color: #b8860b;
  color: #2a2d32;
  border: none;
  border-radius: 8px;
  padding: 12px 20px;
  font-size: 16px;
  font-weight: bold;
  min-height: 44px;
  min-width: 80px;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-right: 20px;
}

.back-button:hover {
  background-color: #9a7209;
  transform: translateY(-2px);
}

.back-button:active {
  transform: translateY(0);
}

.settings-title {
  font-size: 24px;
  margin: 0;
  flex: 1;
}

.settings-content {
  flex: 1;
  padding: 20px;
  overflow-y: auto;
}

/* Settings Sections */
.settings-section {
  background-color: #3c4147;
  border: 2px solid #4c5259;
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 20px;
}

.section-title {
  font-size: 20px;
  margin: 0 0 20px 0;
  color: #b8860b;
  display: flex;
  align-items: center;
  gap: 10px;
}

/* Individual Setting Items */
.setting-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 0;
  border-bottom: 1px solid #4c5259;
  gap: 20px;
}

.setting-item:last-child {
  border-bottom: none;
}

.setting-info {
  flex: 1;
}

.setting-label {
  display: block;
  font-size: 16px;
  font-weight: bold;
  color: white;
  margin-bottom: 4px;
}

.setting-description {
  font-size: 14px;
  color: #aaa;
  margin: 0;
  line-height: 1.4;
}

/* Toggle Buttons */
.toggle-button {
  background-color: #4c5259;
  color: white;
  border: 2px solid #5c626a;
  border-radius: 8px;
  padding: 12px 20px;
  font-size: 14px;
  font-weight: bold;
  min-height: 44px;
  min-width: 120px;
  cursor: pointer;
  transition: all 0.3s ease;
  white-space: nowrap;
}

.toggle-button:hover {
  background-color: #5c626a;
  transform: translateY(-2px);
}

.toggle-button.active {
  background-color: #b8860b;
  color: #2a2d32;
  border-color: #b8860b;
}

.toggle-button.active:hover {
  background-color: #9a7209;
}

/* Volume Control */
.volume-control {
  display: flex;
  align-items: center;
  gap: 12px;
  min-width: 300px;
}

.volume-mute-button {
  background-color: #4c5259;
  border: 2px solid #6c757d;
  border-radius: 8px;
  color: #ffffff;
  font-size: 18px;
  padding: 8px 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 48px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.volume-mute-button:hover {
  background-color: #5a6169;
  border-color: #7c8389;
  transform: translateY(-1px);
}

.volume-mute-button.muted {
  background-color: #dc3545;
  border-color: #c82333;
}

.volume-mute-button.muted:hover {
  background-color: #c82333;
  border-color: #bd2130;
}

.volume-icon {
  font-size: 18px;
  opacity: 0.8;
}

.volume-slider {
  flex: 1;
  height: 8px;
  background-color: #4c5259;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
  -webkit-appearance: none;
  appearance: none;
}

.volume-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 24px;
  height: 24px;
  background-color: #b8860b;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s ease;
}

.volume-slider::-webkit-slider-thumb:hover {
  background-color: #9a7209;
  transform: scale(1.1);
}

.volume-slider:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.volume-slider:disabled::-webkit-slider-thumb {
  background-color: #6c757d;
  cursor: not-allowed;
}

.volume-value {
  font-size: 14px;
  font-weight: bold;
  color: #b8860b;
  min-width: 40px;
  text-align: right;
}

/* WiFi Input Fields */
.wifi-input {
  width: 100%;
  max-width: 300px;
  background-color: #2a2d32;
  border: 2px solid #4c5259;
  border-radius: 8px;
  color: white;
  font-size: 16px;
  padding: 12px 15px;
  min-height: 44px;
  transition: all 0.3s ease;
}

.wifi-input:focus {
  outline: none;
  border-color: #b8860b;
  background-color: #323539;
}

.wifi-input::placeholder {
  color: #888;
}

/* Password Input Container */
.password-input-container {
  position: relative;
  width: 100%;
  max-width: 300px;
}

.password-toggle {
  position: absolute;
  right: 8px;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  color: #b8860b;
  font-size: 16px;
  cursor: pointer;
  padding: 8px;
  border-radius: 4px;
  transition: all 0.3s ease;
}

.password-toggle:hover {
  background-color: rgba(184, 134, 11, 0.1);
}

/* WiFi Connect Button */
.wifi-connect-button {
  background-color: #6b8e23;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 12px 24px;
  font-size: 16px;
  font-weight: bold;
  min-height: 44px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.wifi-connect-button:hover:not(:disabled) {
  background-color: #5a7a1e;
  transform: translateY(-2px);
}

.wifi-connect-button:disabled {
  background-color: #4c5259;
  color: #888;
  cursor: not-allowed;
}

/* Responsive adjustments */
@media screen and (max-width: 800px) {
  .settings-content {
    padding: 15px;
  }

  .settings-section {
    padding: 15px;
  }

  .setting-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
  }

  .volume-control {
    min-width: 250px;
  }

  .wifi-input {
    max-width: 100%;
  }

  .password-input-container {
    max-width: 100%;
  }
}

@media screen and (max-height: 480px) {
  .settings-header {
    min-height: 60px;
    padding: 10px 15px;
  }

  .settings-title {
    font-size: 20px;
  }

  .settings-content {
    padding: 15px;
  }

  .settings-section {
    padding: 15px;
    margin-bottom: 15px;
  }

  .section-title {
    font-size: 18px;
    margin-bottom: 15px;
  }

  .setting-item {
    padding: 12px 0;
  }
}
