/* Touch-optimized Date/Time Picker */
.touch-datetime-picker {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.datetime-section {
  position: relative;
}

.datetime-label {
  display: block;
  font-size: 16px;
  font-weight: bold;
  color: #b8860b;
  margin-bottom: 8px;
}

.datetime-display-button {
  width: 100%;
  background-color: #3c4147;
  border: 2px solid #4c5259;
  border-radius: 8px;
  color: white;
  font-size: 18px;
  padding: 15px;
  min-height: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: left;
}

.datetime-display-button:hover {
  background-color: #4c5259;
  border-color: #b8860b;
}

/* Calendar Popup */
.calendar-popup {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background-color: #2a2d32;
  border: 2px solid #b8860b;
  border-radius: 8px;
  padding: 15px;
  z-index: 1000;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.4);
}

.calendar-header {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  margin-bottom: 15px;
}

.nav-button {
  background-color: #b8860b;
  color: #2a2d32;
  border: none;
  border-radius: 6px;
  width: 32px;
  height: 40px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.nav-button:hover {
  background-color: #9a7209;
  transform: scale(1.05);
}

.month-year {
  font-size: 18px;
  font-weight: bold;
  color: white;
  min-width: 140px;
  text-align: center;
}

.calendar-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 2px;
}

.weekday-header {
  background-color: #3c4147;
  color: #b8860b;
  padding: 8px;
  text-align: center;
  font-size: 12px;
  font-weight: bold;
  border-radius: 4px;
}

.calendar-day {
  background-color: #3c4147;
  color: white;
  border: 1px solid #4c5259;
  border-radius: 4px;
  padding: 10px;
  min-height: 40px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 14px;
  font-weight: bold;
}

.calendar-day:hover:not(.past):not(.empty) {
  background-color: #4c5259;
  transform: scale(1.05);
}

.calendar-day.today {
  background-color: #b8860b;
  color: #2a2d32;
}

.calendar-day.selected {
  background-color: #6b8e23;
  color: white;
  border-color: #6b8e23;
}

.calendar-day.past {
  background-color: #2a2f36;
  color: #6c757d;
  cursor: not-allowed;
}

.calendar-day.empty {
  background-color: transparent;
  border: none;
  cursor: default;
}

/* Time Popup */
.time-popup {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background-color: #2a2d32;
  border: 2px solid #b8860b;
  border-radius: 8px;
  padding: 20px;
  z-index: 1000;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.4);
}

.time-controls {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  margin-bottom: 20px;
}

.time-column {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.time-column label {
  font-size: 14px;
  color: #b8860b;
  font-weight: bold;
}

.time-selector {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
}

.time-nav-button {
  background-color: #b8860b;
  color: #2a2d32;
  border: none;
  border-radius: 4px;
  width: 40px;
  height: 30px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
}

.time-nav-button:hover {
  background-color: #9a7209;
  transform: scale(1.1);
}

.time-display {
  background-color: #3c4147;
  color: white;
  border: 2px solid #4c5259;
  border-radius: 6px;
  padding: 10px 15px;
  font-size: 24px;
  font-weight: bold;
  min-width: 60px;
  text-align: center;
}

.time-separator {
  font-size: 24px;
  font-weight: bold;
  color: white;
  margin-top: 30px;
}

.time-confirm-button {
  width: 100%;
  background-color: #6b8e23;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 12px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
}

.time-confirm-button:hover {
  background-color: #5a7a1e;
  transform: translateY(-2px);
}

/* Responsive adjustments */
@media screen and (max-width: 800px) {
  .calendar-grid {
    gap: 1px;
  }
  
  .calendar-day {
    padding: 8px;
    min-height: 35px;
    font-size: 12px;
  }
  
  .time-controls {
    gap: 15px;
  }
  
  .time-display {
    font-size: 20px;
    padding: 8px 12px;
    min-width: 50px;
  }
}
