/* Events List Component - Touch Optimized */
.events-list {
  width: 100%;
  height: 100vh;
  background-color: #2a2d32;
  color: white;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.events-header {
  display: flex;
  align-items: center;
  padding: 15px 20px;
  background-color: #22252a;
  border-bottom: 2px solid #3c4147;
  min-height: 70px;
}

.back-button {
  background-color: #b8860b;
  color: #2a2d32;
  border: none;
  border-radius: 8px;
  padding: 12px 20px;
  font-size: 16px;
  font-weight: bold;
  min-height: 44px;
  min-width: 80px;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-right: 20px;
}

.back-button:hover {
  background-color: #9a7209;
  transform: translateY(-2px);
}

.back-button:active {
  transform: translateY(0);
}

.events-title {
  font-size: 24px;
  margin: 0;
  flex: 1;
}

.events-content {
  flex: 1;
  padding: 20px;
  overflow-y: auto;
}

/* No Events State */
.no-events {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  text-align: center;
  opacity: 0.7;
}

.no-events-icon {
  font-size: 64px;
  margin-bottom: 20px;
}

.no-events h2 {
  font-size: 24px;
  margin: 10px 0;
  color: #b8860b;
}

.no-events p {
  font-size: 16px;
  margin: 0;
  opacity: 0.8;
}

/* Events Grid */
.events-grid {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.event-card {
  background-color: #3c4147;
  border: 2px solid #4c5259;
  border-radius: 12px;
  padding: 20px;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 20px;
  min-height: 80px;
}

.event-card:hover {
  background-color: #4c5259;
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
}

.event-card.upcoming {
  border-color: #6b8e23;
}

.event-card.past {
  border-color: #6c757d;
  opacity: 0.7;
}

/* Event DateTime */
.event-datetime {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 120px;
  padding: 10px;
  background-color: rgba(184, 134, 11, 0.1);
  border-radius: 8px;
  border: 1px solid rgba(184, 134, 11, 0.3);
}

.event-date {
  font-size: 14px;
  font-weight: bold;
  color: #b8860b;
  margin-bottom: 4px;
}

.event-time {
  font-size: 18px;
  font-weight: bold;
  color: white;
}

/* Event Details */
.event-details {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.event-melody, .event-repeat {
  font-size: 16px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.event-melody {
  font-weight: bold;
  color: #b8860b;
}

.event-repeat {
  color: #cd853f;
}

/* Event Actions */
.event-actions {
  display: flex;
  flex-direction: column;
  gap: 8px;
  min-width: 100px;
}

.edit-button {
  background-color: #cd853f;
  color: #2a2d32;
  border: none;
  border-radius: 6px;
  padding: 10px 15px;
  font-size: 14px;
  font-weight: bold;
  min-height: 40px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.edit-button:hover {
  background-color: #b8722e;
  transform: translateY(-2px);
}

.edit-button:active {
  transform: translateY(0);
}

.delete-button {
  background-color: #dc3545;
  color: white;
  border: none;
  border-radius: 6px;
  padding: 10px 15px;
  font-size: 14px;
  font-weight: bold;
  min-height: 40px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.delete-button:hover {
  background-color: #c82333;
  transform: translateY(-2px);
}

.delete-button:active {
  transform: translateY(0);
}

/* Responsive adjustments */
@media screen and (max-height: 480px) {
  .events-header {
    min-height: 60px;
    padding: 10px 15px;
  }

  .events-title {
    font-size: 20px;
  }

  .events-content {
    padding: 15px;
  }

  .event-card {
    padding: 15px;
    min-height: 70px;
    gap: 15px;
  }

  .event-datetime {
    min-width: 100px;
    padding: 8px;
  }

  .event-date {
    font-size: 12px;
  }

  .event-time {
    font-size: 16px;
  }

  .event-melody, .event-repeat {
    font-size: 14px;
  }

  .event-actions {
    min-width: 90px;
  }

  .edit-button, .delete-button {
    padding: 8px 12px;
    font-size: 12px;
    min-height: 35px;
  }

  .no-events-icon {
    font-size: 48px;
  }

  .no-events h2 {
    font-size: 20px;
  }

  .no-events p {
    font-size: 14px;
  }
}

/* For very narrow screens, stack buttons horizontally */
@media screen and (max-width: 600px) {
  .event-actions {
    flex-direction: row;
    gap: 6px;
    min-width: 120px;
  }

  .edit-button, .delete-button {
    flex: 1;
    padding: 8px 10px;
    font-size: 11px;
  }
}
