/* Raspberry Pi 7" touchscreen layout (800x480) */
.App {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: #2a2d32;
  color: white;
  overflow: hidden;
}



/* Main Menu Layout - Full Screen */
.App-main-menu {
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 25px;
  padding: 40px;
  box-sizing: border-box;
}

.main-menu-button {
  background-color: #3c4147;
  color: white;
  border: 2px solid #4c5259;
  border-radius: 16px;
  padding: 30px 25px;
  font-size: 22px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.main-menu-button:hover {
  background-color: #4c5259;
  transform: translateY(-4px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.4);
}

.main-menu-button:active {
  transform: translateY(-1px);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
}

.main-menu-button.primary {
  background-color: #b8860b;
  color: #2a2d32;
  border-color: #b8860b;
  font-size: 26px;
  box-shadow: 0 6px 20px rgba(184, 134, 11, 0.3);
}

.main-menu-button.primary:hover {
  background-color: #9a7209;
  box-shadow: 0 10px 30px rgba(184, 134, 11, 0.4);
}

/* Legacy control buttons (keeping for compatibility) */
.App-controls {
  height: 80px;
  background-color: #22252a;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 0 20px;
  border-top: 1px solid #3c4147;
}

.control-button {
  background-color: #b8860b;
  color: #2a2d32;
  border: none;
  border-radius: 8px;
  padding: 12px 20px;
  font-size: 16px;
  font-weight: bold;
  min-height: 44px;
  min-width: 100px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.control-button:hover {
  background-color: #9a7209;
  transform: translateY(-2px);
}

.control-button:active {
  transform: translateY(0);
}



/* Responsive adjustments for different screen sizes */
@media screen and (max-width: 800px) {
  .App-main-menu {
    gap: 20px;
    padding: 30px;
  }

  .main-menu-button {
    font-size: 20px;
    padding: 25px 20px;
  }

  .main-menu-button.primary {
    font-size: 24px;
  }
}

@media screen and (max-height: 480px) {
  .App-main-menu {
    gap: 15px;
    padding: 25px;
  }

  .main-menu-button {
    font-size: 18px;
    padding: 20px 15px;
    border-radius: 12px;
  }

  .main-menu-button.primary {
    font-size: 22px;
  }
}

/* For very small screens, stack buttons vertically */
@media screen and (max-width: 600px) {
  .App-main-menu {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, 1fr);
    gap: 15px;
    padding: 20px;
  }
}
