/* Schedule Event Component - Tabbed Interface for 800x480 */
.schedule-event {
  width: 100%;
  height: 100vh;
  background-color: #2a2d32;
  color: white;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.schedule-header {
  display: flex;
  align-items: center;
  padding: 12px 20px;
  background-color: #22252a;
  border-bottom: 2px solid #3c4147;
  min-height: 60px;
  flex-shrink: 0;
}

.back-button {
  background-color: #b8860b;
  color: #2a2d32;
  border: none;
  border-radius: 8px;
  padding: 12px 20px;
  font-size: 16px;
  font-weight: bold;
  min-height: 44px;
  min-width: 80px;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-right: 20px;
}

.back-button:hover {
  background-color: #9a7209;
  transform: translateY(-2px);
}

.back-button:active {
  transform: translateY(0);
}

.schedule-title {
  font-size: 20px;
  margin: 0;
  flex: 1;
}

.editing-indicator {
  background-color: #cd853f;
  color: #2a2d32;
  padding: 6px 10px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: bold;
  display: flex;
  align-items: center;
  gap: 4px;
}

/* Tab Navigation */
.tab-navigation {
  display: flex;
  background-color: #22252a;
  border-bottom: 2px solid #3c4147;
  flex-shrink: 0;
}

.tab-button {
  flex: 1;
  background-color: transparent;
  color: #aaa;
  border: none;
  padding: 12px 8px;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4px;
  position: relative;
  min-height: 60px;
}

.tab-button.active {
  color: #b8860b;
  background-color: #2a2d32;
}

.tab-button.valid {
  color: #6b8e23;
}

.tab-button.active.valid {
  color: #b8860b;
}

.tab-number {
  background-color: #3c4147;
  color: white;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  font-weight: bold;
}

.tab-button.active .tab-number {
  background-color: #b8860b;
  color: #2a2d32;
}

.tab-button.valid .tab-number {
  background-color: #6b8e23;
  color: white;
}

.tab-label {
  font-size: 11px;
  text-align: center;
  line-height: 1.2;
}

.tab-check {
  position: absolute;
  top: 4px;
  right: 4px;
  color: #6b8e23;
  font-size: 12px;
}

/* Tab Content Container */
.tab-content-container {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
}

.tab-content {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.tab-title {
  font-size: 18px;
  color: #b8860b;
  margin: 0 0 20px 0;
  text-align: center;
}

/* Repeat Options Grid */
.repeat-options-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 15px;
  margin-top: 10px;
}

.repeat-option-button {
  background-color: #3c4147;
  color: white;
  border: 2px solid #4c5259;
  border-radius: 12px;
  padding: 20px 15px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: center;
  min-height: 80px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 8px;
}

.repeat-option-button:hover {
  background-color: #4c5259;
  transform: translateY(-2px);
}

.repeat-option-button.active {
  background-color: #b8860b;
  color: #2a2d32;
  border-color: #b8860b;
}

.repeat-option-label {
  font-size: 16px;
  font-weight: bold;
}

.repeat-option-description {
  font-size: 12px;
  opacity: 0.8;
  line-height: 1.3;
}

.repeat-option-button.active .repeat-option-description {
  opacity: 0.7;
}

/* Touch-friendly input styling */
.touch-input {
  background-color: #3c4147;
  border: 2px solid #4c5259;
  border-radius: 8px;
  color: white;
  font-size: 18px;
  padding: 15px;
  min-height: 50px;
  transition: all 0.3s ease;
}

.touch-input:focus {
  outline: none;
  border-color: #b8860b;
  background-color: #4c5259;
}

.date-input, .time-input {
  width: 100%;
  max-width: 300px;
}

/* Repeat Options */
.repeat-options {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
}

.repeat-button {
  background-color: #3c4147;
  color: white;
  border: 2px solid #4c5259;
  border-radius: 8px;
  padding: 15px 20px;
  font-size: 16px;
  font-weight: bold;
  min-height: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.repeat-button:hover {
  background-color: #4c5259;
  transform: translateY(-2px);
}

.repeat-button.active {
  background-color: #b8860b;
  color: #2a2d32;
  border-color: #b8860b;
}

.repeat-button:active {
  transform: translateY(0);
}

/* Melody Selection in Tab */
.melody-categories {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 10px;
  margin-bottom: 20px;
}

.category-tab {
  background-color: #3c4147;
  color: white;
  border: 2px solid #4c5259;
  border-radius: 8px;
  padding: 12px 8px;
  font-size: 12px;
  font-weight: bold;
  min-height: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1.2;
}

.category-tab:hover {
  background-color: #4c5259;
}

.category-tab.active {
  background-color: #b8860b;
  color: #2a2d32;
  border-color: #b8860b;
}

.melody-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
  flex: 1;
  overflow-y: auto;
}

.melody-option {
  background-color: #3c4147;
  color: white;
  border: 2px solid #4c5259;
  border-radius: 8px;
  padding: 15px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: left;
  min-height: 60px;
}

.melody-option:hover {
  background-color: #4c5259;
  transform: translateY(-2px);
}

.melody-option.active {
  background-color: #b8860b;
  color: #2a2d32;
  border-color: #b8860b;
}

.melody-option:active {
  transform: translateY(0);
}

.melody-name {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 4px;
}

.melody-description {
  font-size: 12px;
  opacity: 0.8;
}

.melody-option.active .melody-description {
  opacity: 0.7;
}

/* Legacy melody grid (keeping for compatibility) */
.melody-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
}

.melody-button {
  background-color: #3c4147;
  color: white;
  border: 2px solid #4c5259;
  border-radius: 8px;
  padding: 15px 12px;
  font-size: 14px;
  font-weight: bold;
  min-height: 60px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.melody-button:hover {
  background-color: #4c5259;
  transform: translateY(-2px);
}

.melody-button.active {
  background-color: #b8860b;
  color: #2a2d32;
  border-color: #b8860b;
}

.melody-button:active {
  transform: translateY(0);
}

/* Tab Navigation Buttons */
.tab-navigation-buttons {
  display: flex;
  gap: 15px;
  padding: 20px;
  background-color: #22252a;
  border-top: 2px solid #3c4147;
  flex-shrink: 0;
}

.nav-button {
  flex: 1;
  border: none;
  border-radius: 8px;
  padding: 15px 20px;
  font-size: 16px;
  font-weight: bold;
  min-height: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.nav-button.secondary {
  background-color: #6c757d;
  color: white;
}

.nav-button.secondary:hover {
  background-color: #5a6268;
  transform: translateY(-2px);
}

.nav-button.primary.enabled {
  background-color: #6b8e23;
  color: white;
}

.nav-button.primary.enabled:hover {
  background-color: #5a7a1e;
  transform: translateY(-2px);
}

.nav-button.primary.disabled {
  background-color: #4c5259;
  color: #6c757d;
  cursor: not-allowed;
}

.nav-button:active {
  transform: translateY(0);
}

/* Legacy styles for compatibility */
.form-actions {
  display: flex;
  gap: 15px;
  margin-top: auto;
  padding-top: 20px;
}

.cancel-button, .save-button {
  flex: 1;
  border: none;
  border-radius: 8px;
  padding: 15px 20px;
  font-size: 18px;
  font-weight: bold;
  min-height: 55px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.cancel-button {
  background-color: #6c757d;
  color: white;
}

.cancel-button:hover {
  background-color: #5a6268;
  transform: translateY(-2px);
}

.save-button.enabled {
  background-color: #6b8e23;
  color: white;
}

.save-button.enabled:hover {
  background-color: #5a7a1e;
  transform: translateY(-2px);
}

.save-button.disabled {
  background-color: #4c5259;
  color: #6c757d;
  cursor: not-allowed;
}

.cancel-button:active, .save-button:active {
  transform: translateY(0);
}

/* Responsive adjustments for 7" screen (480px height) */
@media screen and (max-height: 480px) {
  .schedule-header {
    min-height: 50px;
    padding: 8px 15px;
  }

  .schedule-title {
    font-size: 18px;
  }

  .editing-indicator {
    padding: 4px 8px;
    font-size: 11px;
  }

  .tab-navigation {
    min-height: 50px;
  }

  .tab-button {
    padding: 8px 6px;
    min-height: 50px;
  }

  .tab-number {
    width: 20px;
    height: 20px;
    font-size: 11px;
  }

  .tab-label {
    font-size: 10px;
  }

  .tab-content-container {
    padding: 15px;
  }

  .tab-title {
    font-size: 16px;
    margin-bottom: 15px;
  }

  .repeat-options-grid {
    gap: 12px;
  }

  .repeat-option-button {
    padding: 15px 12px;
    min-height: 70px;
  }

  .repeat-option-label {
    font-size: 14px;
  }

  .repeat-option-description {
    font-size: 11px;
  }

  .melody-categories {
    gap: 8px;
    margin-bottom: 15px;
  }

  .category-tab {
    padding: 10px 6px;
    font-size: 11px;
    min-height: 45px;
  }

  .melody-list {
    gap: 10px;
  }

  .melody-option {
    padding: 12px;
    min-height: 50px;
  }

  .melody-name {
    font-size: 14px;
  }

  .melody-description {
    font-size: 11px;
  }

  .tab-navigation-buttons {
    padding: 15px;
  }

  .nav-button {
    padding: 12px 16px;
    font-size: 14px;
    min-height: 45px;
  }
}

/* Very small screens */
@media screen and (max-width: 600px) {
  .melody-categories {
    grid-template-columns: 1fr;
    gap: 8px;
  }

  .repeat-options-grid {
    grid-template-columns: 1fr;
    gap: 10px;
  }
}
