/* Toast Notification System */
.toast-container {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 9999;
  display: flex;
  flex-direction: column;
  gap: 10px;
  pointer-events: none;
}

.toast {
  background-color: #3c4147;
  border: 2px solid #4c5259;
  border-radius: 12px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.4);
  min-width: 300px;
  max-width: 400px;
  pointer-events: auto;
  transform: translateX(100%);
  opacity: 0;
  transition: all 0.3s ease;
}

.toast-enter {
  transform: translateX(0);
  opacity: 1;
}

.toast-exit {
  transform: translateX(100%);
  opacity: 0;
}

.toast-content {
  display: flex;
  align-items: center;
  padding: 15px 20px;
  gap: 12px;
}

.toast-icon {
  font-size: 20px;
  flex-shrink: 0;
}

.toast-message {
  flex: 1;
  color: white;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.4;
}

.toast-close {
  background: none;
  border: none;
  color: #aaa;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
  padding: 0;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: all 0.3s ease;
  flex-shrink: 0;
}

.toast-close:hover {
  background-color: rgba(255, 255, 255, 0.1);
  color: white;
}

/* Toast Type Variants */
.toast-success {
  border-color: #6b8e23;
  background-color: rgba(107, 142, 35, 0.1);
}

.toast-success .toast-content {
  background: linear-gradient(90deg, rgba(107, 142, 35, 0.2) 0%, transparent 100%);
}

.toast-error {
  border-color: #dc3545;
  background-color: rgba(220, 53, 69, 0.1);
}

.toast-error .toast-content {
  background: linear-gradient(90deg, rgba(220, 53, 69, 0.2) 0%, transparent 100%);
}

.toast-warning {
  border-color: #cd853f;
  background-color: rgba(205, 133, 63, 0.1);
}

.toast-warning .toast-content {
  background: linear-gradient(90deg, rgba(205, 133, 63, 0.2) 0%, transparent 100%);
}

.toast-info {
  border-color: #b8860b;
  background-color: rgba(184, 134, 11, 0.1);
}

.toast-info .toast-content {
  background: linear-gradient(90deg, rgba(184, 134, 11, 0.2) 0%, transparent 100%);
}

/* Responsive adjustments for smaller screens */
@media screen and (max-width: 800px) {
  .toast-container {
    top: 10px;
    right: 10px;
    left: 10px;
  }

  .toast {
    min-width: auto;
    max-width: none;
    width: 100%;
  }

  .toast-content {
    padding: 12px 15px;
  }

  .toast-message {
    font-size: 13px;
  }

  .toast-icon {
    font-size: 18px;
  }
}

/* Animation for multiple toasts */
.toast:nth-child(1) {
  animation-delay: 0ms;
}

.toast:nth-child(2) {
  animation-delay: 100ms;
}

.toast:nth-child(3) {
  animation-delay: 200ms;
}

.toast:nth-child(4) {
  animation-delay: 300ms;
}

.toast:nth-child(5) {
  animation-delay: 400ms;
}

/* Hover to pause auto-dismiss */
.toast:hover {
  transform: translateX(-5px) scale(1.02);
  box-shadow: 0 12px 35px rgba(0, 0, 0, 0.5);
}

/* Touch-friendly close button for Pi touchscreen */
@media (pointer: coarse) {
  .toast-close {
    width: 32px;
    height: 32px;
    font-size: 18px;
  }
}
